.banner {
  position: relative;
  height: 448px;
  color: white;
  object-fit: contain;
}
.banner__contents {
    margin-left: 30px;
    padding-top: 250px; /* Increase top padding to move text down */
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  
  .banner__title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.5rem;
    margin-top: 20px; /* Space below the logo */
  }
  
  .banner__description {
    padding-top: 1rem;
    width: 45rem;
    line-height: 1.3;
    font-size: 1rem; /* Increased font size for better visibility */
    max-width: 500px;
    height: auto;
  }

.banner__buttons {
  display: flex;
  gap: 1rem;
}

.banner__button {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding: 0.5rem 2rem;
  margin-right: 1rem;
  background-color: rgba(51, 51, 51, 0.5);
  transition: all 0.2s;
}

.banner__button:hover {
  color: #000;
  background-color: #e6e6e6;
  transform: scale(1.05);
}

/* Optional: Add a gradient overlay at the bottom of the banner */
.banner::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 7.4rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
  );
}

.banner__fadeButton {
  height: 7.4rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
  );
  position: absolute;
  bottom: 0;
  width: 100%;
}
